html, body {
  height: 100%;
  overflow-y: hidden;
}

body {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
}

.App {
  width: 360px;
  height: 640px;
  background-color: #f5f5f5;
  overflow-y: scroll;
  overflow-x: hidden;
}

#content {
  background-color: #ccc;
  width: 100%;
  height: 200%;
  display: block;
  padding: 15px;
  margin-top: 80px;
  overflow-x: hidden;
}

.nav {
  height: 56px;
  /*padding-top: 24px;*/
  background: #f7fafc;
  border-bottom: 2px solid #e6e8ea;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  position: fixed;
}

#search-area {
  height: 56px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#search-area input{
  height: 56px;
  border: none;
  background: none;
  width: 225px;
  font-size: 14pt;
}

#search {
  width: 40px;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #3b3b3b;
}

#control-area {
  height: 56px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

#control-area .nav-button {
  width: 48px;
  height: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  color: #3b3b3b;
}

#control-area .nav-button:active {
  background-color: #e5e5e5;
}

::-webkit-input-placeholder {
  font-size: 14pt;
  color: #666;
  font-weight: normal;
}

::-moz-placeholder {
  font-size: 14pt;
  color: #666;
  font-weight: normal;
}

:focus {
  outline: none;
}

i {
  -webkit-user-select: all;  /* Chrome 49+ */
  -moz-user-select: all;     /* Firefox 43+ */
  -ms-user-select: all;      /* No support yet */
  user-select: all;          
}


